
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
      Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
    -webkit-font-smoothing: antialiased;
    background-color: #f8f8f8;
}
  
  /* Global button style */
.button {
    font-family: inherit;
    text-align: center;
    cursor: pointer;
    border: none;
    text-decoration: none;
    outline: none;
    color: #ffffff;
    background-color: rgb(0, 120, 212);
    padding: 0.5rem 1.2rem;
    border-radius: 2px;
    font-size: 1rem;
    min-width: 6rem;
}
.button:hover {
    background-color: rgb(16, 110, 190);
}
.button.disabled {
    pointer-events: none;
    background-color: #cccccc;
    color: #666666;
}
  
  /* Main section */
.main {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.main .title h3 {
    font-size: 2.3rem;
    font-weight: 300;
    margin: 0.8rem 0;
}
.hidden {
    display: none;
}
.reveal {
    opacity: 0;
}
.reveal:hover {
    opacity: 0.2;
}
  
  /* Upload box */
.upload-box {
    font-size: 0.8rem;
    color: #666666;
    cursor: pointer;
    width: 16rem;
    height: 10rem;
    background: #fff;
    border: 0.1rem dashed #838388;
    border-radius: 0.4rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    margin: 1rem 0 2rem 0;
}
.upload-box.dragover {
    /* background-color: grey; */
    color: #eeeeee;
    border: 0.1rem solid rgb(0, 120, 212);
    -webkit-box-shadow: inset 0 0 0 0.1rem rgb(0, 120, 212);
            box-shadow: inset 0 0 0 0.1rem rgb(0, 120, 212);
}
.upload-box:hover {
    border-color: rgb(0, 120, 212);
}
.upload-box #image-preview {
    max-width: 14rem;
    max-height: 8rem;
    -webkit-box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
}
#image-result {
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    max-height: 20rem;
}
#image-box {
    position: relative;
    width: auto;
    float: left;
    margin-bottom: 2rem;
}
#image-display {
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    max-height: 20rem;
}
#image-display.loading {
    -webkit-filter: brightness(30%);
            filter: brightness(30%);
}
#pred-result {
    color: white;
    font-size: 1.5rem;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
#loader {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 10;
    margin: 0 auto;
}
  
  /* Animation */
#spinner {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    stroke: #cccccc;
    stroke-width: 3px;
    -webkit-transform-origin: 50%;
            transform-origin: 50%;
    -webkit-animation: line 1.6s cubic-bezier(0.4, 0, 0.2, 1) infinite,
      rotate 1.6s linear infinite;
            animation: line 1.6s cubic-bezier(0.4, 0, 0.2, 1) infinite,
      rotate 1.6s linear infinite;
}
@-webkit-keyframes rotate {
from {
      -webkit-transform: rotate(0);
              transform: rotate(0);
}
to {
      -webkit-transform: rotate(450deg);
              transform: rotate(450deg);
}
}
@keyframes rotate {
from {
      -webkit-transform: rotate(0);
              transform: rotate(0);
}
to {
      -webkit-transform: rotate(450deg);
              transform: rotate(450deg);
}
}
@-webkit-keyframes line {
0% {
      stroke-dasharray: 2, 85.964;
      -webkit-transform: rotate(0);
              transform: rotate(0);
}
50% {
      stroke-dasharray: 65.973, 21.9911;
      stroke-dashoffset: 0;
}
100% {
      stroke-dasharray: 2, 85.964;
      stroke-dashoffset: -65.973;
      -webkit-transform: rotate(90deg);
              transform: rotate(90deg);
}
}
@keyframes line {
0% {
      stroke-dasharray: 2, 85.964;
      -webkit-transform: rotate(0);
              transform: rotate(0);
}
50% {
      stroke-dasharray: 65.973, 21.9911;
      stroke-dashoffset: 0;
}
100% {
      stroke-dasharray: 2, 85.964;
      stroke-dashoffset: -65.973;
      -webkit-transform: rotate(90deg);
              transform: rotate(90deg);
}
}
  